import React, {
  useRef,
  useState,
  useEffect,
  forwardRef,
  useImperativeHandle
} from 'react';
import {
  Stage,
  Layer,
  Line,
  Rect,
  Circle,
  Arrow,
  Text,
  RegularPolygon,
  Image as KonvaImage
} from 'react-konva';

import useImage from 'use-image';
import Konva from 'konva';
import { KonvaEventObject } from 'konva/lib/Node';

export type ShapeType = 'brush' | 'eraser' | 'rect' | 'circle' | 'triangle' | 'arrow' | 'text';

export type CanvasPageHandle = {
  undo: () => void;
  redo: () => void;
  bringForward: () => void;
  sendBackward: () => void;
};

type CanvasPageProps = {
  color: string;
  thickness: number;
  mode: ShapeType;
  zoom: number;
  fontSize: number;
  fontFamily: string;
};

const DroppedImage = ({ src, x, y, width, height, isSelected, onClick, onDragEnd }: any) => {
  const [image] = useImage(src);
  return (
    <KonvaImage
      image={image}
      x={x}
      y={y}
      width={width}
      height={height}
      draggable
      stroke={isSelected ? 'blue' : undefined}
      strokeWidth={isSelected ? 1 : 0}
      onClick={onClick}
      onDragEnd={onDragEnd}
    />
  );
};

const CanvasPage = forwardRef<CanvasPageHandle, CanvasPageProps>(
  ({ color, thickness, mode, zoom, fontSize, fontFamily }, ref) => {
    const [elements, setElements] = useState<any[]>([]);
    const [redoStack, setRedoStack] = useState<any[]>([]);
    const [current, setCurrent] = useState<any>(null);
    const [selectedId, setSelectedId] = useState<number | null>(null);
    const [editingId, setEditingId] = useState<number | null>(null);
    const isDrawing = useRef(false);
    const stageRef = useRef<any>(null);

    useEffect(() => {
      setSelectedId(null);
      setEditingId(null);
    }, [mode]);

    useImperativeHandle(ref, () => ({ undo, redo, bringForward, sendBackward }));

    const undo = () => {
      if (elements.length === 0) return;
      const newElements = [...elements];
      const last = newElements.pop();
      setElements(newElements);
      setRedoStack([last, ...redoStack]);
    };

    const redo = () => {
      if (redoStack.length === 0) return;
      const [first, ...rest] = redoStack;
      setElements([...elements, first]);
      setRedoStack(rest);
    };

    const bringForward = () => {
      if (selectedId === null || selectedId >= elements.length - 1) return;
      const newElements = [...elements];
      const temp = newElements[selectedId];
      newElements[selectedId] = newElements[selectedId + 1];
      newElements[selectedId + 1] = temp;
      setElements(newElements);
      setSelectedId(selectedId + 1);
    };

    const sendBackward = () => {
      if (selectedId === null || selectedId <= 0) return;
      const newElements = [...elements];
      const temp = newElements[selectedId];
      newElements[selectedId] = newElements[selectedId - 1];
      newElements[selectedId - 1] = temp;
      setElements(newElements);
      setSelectedId(selectedId - 1);
    };

    const handleDropImage = (e: React.DragEvent<HTMLDivElement>) => {
      e.preventDefault();
      console.log(' Image dropped');

      const stage = stageRef.current;
      const pointerPos = stage?.getPointerPosition();
      if (!stage || !pointerPos) return;

      const dropX = pointerPos.x / zoom;
      const dropY = pointerPos.y / zoom;

      const files = Array.from(e.dataTransfer.files);
      const imageFiles = files.filter(file => file.type.startsWith('image/'));

      if (imageFiles.length === 0) return;

      imageFiles.forEach((file, index) => {
        const reader = new FileReader();
        reader.onload = () => {
          const src = reader.result as string;
          const offset = index * 30;
          const img = new window.Image();
          img.onload = () => {
            setElements(prev => [
              ...prev,
              {
                type: 'image',
                src,
                x: dropX + offset,
                y: dropY + offset,
                width: img.width / 2,
                height: img.height / 2,
              }
            ]);
          };
          img.src = src;
        };
        reader.readAsDataURL(file);
      });
    };

    const handleMouseDown = (e: any) => {
      if (mode === 'text') {
        const clickedOnShape = e.target !== e.target.getStage();
        if (!clickedOnShape) {
          const pos = e.target.getStage().getPointerPosition();
          const newText = prompt('Enter text:') || '';
          setElements([
            ...elements,
            {
              type: 'text',
              x: pos.x,
              y: pos.y,
              text: newText,
              fontSize,
              fontFamily,
              fill: color,
            },
          ]);
          setRedoStack([]);
        }
        return;
      }

      isDrawing.current = true;
      const pos = e.target.getStage().getPointerPosition();
      setSelectedId(null);
      setEditingId(null);

      switch (mode) {
        case 'brush':
        case 'eraser':
          setCurrent({
            type: 'line',
            points: [pos.x, pos.y],
            stroke: color,
            strokeWidth: thickness,
            globalCompositeOperation: mode === 'eraser' ? 'destination-out' : 'source-over',
          });
          break;
        case 'rect':
          setCurrent({ type: 'rect', x: pos.x, y: pos.y, width: 0, height: 0, stroke: color, strokeWidth: thickness });
          break;
        case 'circle':
        case 'triangle':
          setCurrent({ type: mode, x: pos.x, y: pos.y, radius: 0, stroke: color, strokeWidth: thickness });
          break;
        case 'arrow':
          setCurrent({ type: 'arrow', points: [pos.x, pos.y, pos.x, pos.y], stroke: color, strokeWidth: thickness });
          break;
      }
    };

    const handleMouseMove = (e: any) => {
      if (!isDrawing.current || !current) return;
      const pos = e.target.getStage().getPointerPosition();

      if (current.type === 'line') {
        setCurrent({ ...current, points: [...current.points, pos.x, pos.y] });
      } else if (current.type === 'rect') {
        setCurrent({ ...current, width: pos.x - current.x, height: pos.y - current.y });
      } else if (current.type === 'circle' || current.type === 'triangle') {
        const dx = pos.x - current.x;
        const dy = pos.y - current.y;
        setCurrent({ ...current, radius: Math.sqrt(dx * dx + dy * dy) });
      } else if (current.type === 'arrow') {
        const newPoints = [current.points[0], current.points[1], pos.x, pos.y];
        setCurrent({ ...current, points: newPoints });
      }
    };

    const handleMouseUp = () => {
      if (current) {
        setElements([...elements, current]);
        setRedoStack([]);
        setCurrent(null);
      }
      isDrawing.current = false;
    };

    return (
      <div
        onDrop={handleDropImage}
        onDragOver={(e) => {
          e.preventDefault();
        }}
        style={{ width: '100%', height: '500px' }}
      >
        <Stage
          ref={stageRef}
          width={window.innerWidth - 40}
          height={500}
          scaleX={zoom}
          scaleY={zoom}
          onMouseDown={handleMouseDown}
          onMousemove={handleMouseMove}
          onMouseup={handleMouseUp}
          onMouseDownCapture={(e: KonvaEventObject<MouseEvent>) => {
            if (e.target === e.target.getStage()) {
              setSelectedId(null);
              setEditingId(null);
            }
          }}
          style={{ border: '1px solid #ccc', background: 'white' }}
        >
          <Layer>
            {[...elements, current].filter(Boolean).map((el, i) => {
              const isSelected = i === selectedId;
              switch (el.type) {
                case 'line':
                  return <Line key={i} {...el} lineCap="round" tension={0.5} globalCompositeOperation={el.globalCompositeOperation} onClick={() => setSelectedId(i)} />;
                case 'rect':
                  return <Rect key={i} {...el} stroke={isSelected ? 'blue' : el.stroke} dash={isSelected ? [4, 4] : []} onClick={() => setSelectedId(i)} />;
                case 'circle':
                  return <Circle key={i} {...el} stroke={isSelected ? 'blue' : el.stroke} dash={isSelected ? [4, 4] : []} onClick={() => setSelectedId(i)} />;
                case 'triangle':
                  return <RegularPolygon key={i} x={el.x} y={el.y} sides={3} radius={el.radius} strokeWidth={el.strokeWidth} stroke={isSelected ? 'blue' : el.stroke} dash={isSelected ? [4, 4] : []} onClick={() => setSelectedId(i)} />;
                case 'arrow':
                  return <Arrow key={i} {...el} onClick={() => setSelectedId(i)} />;
                case 'text':
                  return (
                    <Text
                      key={i}
                      {...el}
                      draggable
                      stroke={isSelected ? 'blue' : undefined}
                      strokeWidth={isSelected ? 0.5 : 0}
                      onClick={() => setSelectedId(i)}
                      onDblClick={() => setEditingId(i)}
                      onDragEnd={(e) => {
                        const newElements = [...elements];
                        newElements[i] = { ...newElements[i], x: e.target.x(), y: e.target.y() };
                        setElements(newElements);
                      }}
                    />
                  );
                case 'image':
                  return (
                    <DroppedImage
                      key={i}
                      src={el.src}
                      x={el.x}
                      y={el.y}
                      width={el.width}
                      height={el.height}
                      isSelected={isSelected}
                      onClick={() => setSelectedId(i)}
                      onDragEnd={(e: any) => {
                        const newElements = [...elements];
                        newElements[i] = { ...newElements[i], x: e.target.x(), y: e.target.y() };
                        setElements(newElements);
                      }}
                    />
                  );
                default:
                  return null;
              }
            })}
          </Layer>
        </Stage>
      </div>
    );
  }
);

export default CanvasPage;
